$mobile-width: 400px;
$tablet-width: 768px;
$desktop-width: 1024px;

@mixin mobile {
  @media screen and (min-width: #{$mobile-width}) and (max-width: #{$tablet-width - 1px}) and (orientation: portrait) {
    @content; } }

@mixin tablet {
  @media screen and (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px})   and (orientation: portrait) {
    @content; } }


@mixin desktop {
  @media screen and (min-width: #{$desktop-width}) and (orientation: portrait) {
    @content; } }


